@import url('https://fonts.googleapis.com/css?family=Lato');

h1 {
  padding-top: 10px;
  color: #3CA1E0;
  text-transform: uppercase;
  font-size: 30px;
  font-family: 'Lato', sans-serif;
}

h3 {
  font-weight: 350;
}

h4 {
  font-weight: lighter;
}

p {
  font-weight: lighter;
}

img {
  width: 750px;
  height: auto;
  object-fit: contain;
}

.projectpage-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.intro-div {
  width: 750px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px;
  padding: 30px 0px 30px 0px;
}

.specs-div {
  width: 750px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px;
  padding: 30px 0px 60px 0px;
}

.specs-lists {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.link button {
  background-color: #DBDDD8;
  width: 150px;
  height: 30px;
  text-transform: uppercase;
  margin-bottom: 30px;
  border: 0.5px solid grey;
}
